import { Block, Locales, Reward } from './utility.model'

export function BlockMock(options?: Partial<Block>): Block {
  return {
    number: options?.number || 0,
    timestamp: options?.timestamp || 0,
  }
}

export function RewardMock(options?: Partial<Reward>): Reward {
  return {
    tokenId: options?.tokenId || 'TOKEN_ID',
    amount: options?.amount || '0',
    amountUSD: options?.amountUSD || '0',
    APR: options?.APR || 0,
    percentage: options?.percentage || 0,
  }
}

export function LocalesMock(options?: Partial<Locales<any>>): Locales<any> {
  return (
    options || {
      en_EN: '',
    }
  )
}
